<template>
  <transition>
    <div class="container" v-if="flag == true">
      <div class="top">
        <i @click="retuan" class="iconfont icon-gengduo"></i>
      </div>
      <div class="main back">
        <van-field
          v-model="message"
          rows="2"
          autosize
          label=""
          type="textarea"
          maxlength="50"
          placeholder=""
          show-word-limit
        />

        <van-button type="primary" @click="submits" block></van-button>
      </div>
    </div>
  </transition>
</template>

<script>
import { Toast } from "vant";
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      flag: false,
      message: ""
    };
  },
  methods: {
    retuan() {
      window.history.back();  
    },
    submits() {
      Toast(``);
      this.message = ""
    }
  },
  created() {
    this.flag = true;
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

//
.sections .hash .router-link-exact-active {
  color: #ff4a4a;
}
</style>